import React from 'react'
import { NavLink } from 'react-router-dom'
import dot from '../../img/img-dot@2x.png'
import './review.css'

const Review = () =>
{
    const nama = JSON.parse( localStorage.getItem( "name" ) );
    const email = JSON.parse( localStorage.getItem( "email" ) );
    const phone = JSON.parse( localStorage.getItem( "phone" ) );
    const nationality = JSON.parse( localStorage.getItem( "nationality" ) );
    const message = JSON.parse( localStorage.getItem( "message" ) );
    const deleteItems = () =>
    {
        localStorage.clear();
    }
    return (
        <div className="container-fluid m-0">
            <div className="row vh-100 align-items-center justify-content-center">
                <div className="card card-review p-3 p shadow position-absolute">
                    <div className="card-body">
                        <div className="right-message">
                            <table>
                                <tbody>
                                    <tr>
                                        <td>Full Name</td>
                                        <td>: { nama }</td>
                                    </tr>
                                    <tr>
                                        <td>Email Address</td>
                                        <td>: { email }</td>
                                    </tr>
                                    <tr>
                                        <td>Phone Number</td>
                                        <td>: { phone }</td>
                                    </tr>
                                    <tr>
                                        <td>Nationality</td>
                                        <td>: { nationality }</td>
                                    </tr>
                                </tbody>
                            </table>
                            <p className="card-text"></p>
                            <p id="message">{ message }</p>
                            <p></p>
                        </div>
                        <hr className="c-hr" />
                        <div className="footer">
                            <h5>
                                Thanks for contacting us! <br />
                                We will be in touch with you shortly.
                            </h5>
                            <NavLink
                                onClick={ deleteItems }
                                className="submit btn text-white"
                                name="submit"
                                type="submit"
                                to='/'
                            >
                                Home
                            </NavLink>
                        </div>
                    </div>
                </div>
                <div className="img">
                    <img
                        src={ dot }
                        className="img-logo position-absolute"
                        alt=''
                    />
                </div>
                <div className="bg-clor">
                    <div className="bg"></div>
                </div>
            </div>
        </div>

    )
}

export default Review